import * as yup from "yup";

export const supportSchema = yup.object().shape({
  phone: yup
    .string()
    .max(8, "   8 ")
    .required("  ")
    .matches(
      new RegExp(`^(774|775|777|778|779|533|219|557)[0-9]{5}$`),
      "   "
    ),
  name: yup
    .string()
    .matches(
      new RegExp(
        `^[-][-]*([-][-][-]*)?(\\s[-]{1}[-]{0,})?(\\s[-]{1}[-]{0,})?$`
      ),
      "  "
    )
    .required(" "),
});

export const packageSchema = yup.object().shape({
  name: yup
    .string()
    .matches(
      new RegExp(
        `^[-][-]*([-][-][-]*)?(\\s[-]{1}[-]{0,})?(\\s[-]{1}[-]{0,})?$`
      ),
      "  "
    )
    .required(" "),
  phone: yup
    .string()
    .max(8, "   8 ")
    .required("  ")
    .matches(
      new RegExp(`^(774|775|777|778|779|533|219|557)[0-9]{5}$`),
      "   "
    ),
  addres: yup.string().required(" "),
  package: yup.string().required(),
});
